<template>
  <div>
     <h2>我是About的内容</h2>
  </div>
</template>

<script>
export default {
name:'AbouT',
//通过路由规则，进入该组件时被调用
beforeRouteEnter(to,from,next){
console.log('beforerouteenter', to, from);
    if (to.meta.isAuth) { //判断当前路由是否需要权限控制
        if (localStorage.getItem('school') === 'atguigu') { //权限控制的具体规则
            next() //放行
        } else {
            alert('学校名不对,无权限查看!')
        }
    } else {
        next()
    }
},
// //通过路由规则，离开该组件时被调用
beforeRouteLeave(to,from,next){
   console.log('beforerouteenter', to, from)
   next()
}
}
</script>

